/*! RESET */
/* Resets the box size of every element */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Gives the body element a parent height to compare against */
  html {
    height: 100%;
  }
  
  body {
    min-height: 100%;
    line-height: 1;
    font-family: sans-serif;
  }
  
  h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
  }
  
  /* Matches the font of special elements to the rest of the page */
  input, select, option, optgroup, textarea, button,
  pre, code {
    font-size: 100%;
    font-family: inherit;
  }
  
  /* Removes default bullet points from lists */
  ol, ul {
    list-style: none;
  }

  




/*! MAIN STYLING */
 /* HEADER */
#header {
    padding: 4vw;
    padding-top: 50px;
    padding-bottom: 10px;

    background-color: rgb(32, 22, 56);
}

/* hide breaks (will use for mobile */
.header_mobile_br {
    display: none;
}
#about_me_mobile_br {
    display: none;
}

/* FOOTER */
#allButFooter-div {
    min-height: calc(100vh - 113px);
}

.footer {
    padding: 4vw;
    padding-top: 15px;
    padding-bottom: 25px;

    border-top: 10px solid rgba(255, 255, 255, 0.25);

    background-color: rgb(32, 22, 56);
}

.footer p {
    color:rgba(255, 255, 255, 0.4);
    /* font-style: italic; */
    font-size: 14px;

    display: flex;
    justify-content: center;
}

#footer-content {
    margin: auto;
}

#footer-content ul{
    margin: auto;
}

#footer-links-div {
    width: 100%;
    margin: auto;
    text-align: center;
}

/* makes it so there's no underline under the hyperlink */
#header a {
    text-decoration: none;
}
a:hover {
    opacity: .7;
}

/* puts header into row not column and changes font color */
#header h1,nav,p,li,ul,a {
    display: inline-block;

    color: aliceblue;
}

/* moves header nav links to right side and makes font smaller */
.head_nav {
    float: right;

    font-size: 20px;
}

/* makes title large */
h1 {
    font-size: 35px;
}

/* adds padding on either side of the "|" in the heading nav links */
#header p {
    padding-right: 18px;
    padding-left: 18px;
}

/* sets background color */
body {
    /* background-color: rgb(39, 39, 39); */
    background-color: rgb(163, 158, 162);
}

/* subtitle */
body h2 {
    width: 100%;

    padding-left: 4vw;
    padding-top: 10px;
    padding-bottom: 10px;

    font-size: 22px;
    color: rgb(240, 248, 255,.6);
    text-decoration: none;

    background-color: rgb(32, 22, 56, 0.6);
}

/* creates the sections (ie:about me, work,contact me) */
.section {
    display: flex;
    justify-content: flex-start;

    padding: 10px;
}

div.content {
    width: 100%;
}

/* creates the title part of the cards to the left of each section */
.content_heading {
    width:150px;
    height: 100%;

    margin-left: 5vw;
    padding: 20px;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;

    text-align: right;
    font-size:20px;
    color: rgb(39, 39, 39);

    background-color: rgb(245, 245, 245,.9);
}

/* creates the content part of the cards to the right of each section */
.content {
    margin-right: 5vw;
    padding: 20px;    
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    border-left: 10px solid rgb(55, 38, 95);
    
    text-align: left;
    font-size: 16px;
    color: rgb(0, 0, 0);

    background-color: rgb(245, 245, 245,.65);
}

.content p {
    font-size: 16px;
    color: rgb(0, 0, 0);
    line-height: 1.4;
}

/* controls the images in the work section */
.content_images {
    width: 100%;
    min-width: 300px;

    border-radius: 25px;
    border: 4px solid rgb(163, 158, 162);
}

/* controls the images in the work section that are FEATURED*/
.featured_work .content_images {
    width: 100%;
    min-width: 300px;
    height:auto;
}

/* image cards for work section */
.content_image_cards {
    border: none;
    display: flex;
    flex-wrap: wrap;
}

/* featured work card */
.work_ex_card_feat {
    display: flex;
    flex-wrap: wrap;
    flex: 2 0 0;
    margin-bottom: 0%;
    padding: 5px;
    position: relative;
    /* max-width: 1000px; */

    text-align: center;
}

/* each individual work card */
.work_ex_card {
    display: flex;
    flex-wrap: wrap;
    flex: 1 0 0;
    margin-bottom: 0%;
    padding: 5px;
    position: relative;
    max-width: 500px;

    text-align: center;
}

/* the title that goes with the featured card */
.work_ex_card_feat h4 {
    position:absolute;
    width: 60%;
    bottom: 50px;
    left: 5px;

    border-top-right-radius: 12.5px;
    border-bottom-right-radius: 12.5px;
    
    padding:2%;
    
    font-size: 20px;
    color: black;

    background-color: rgb(163, 158, 162,.75);
}

/* the title that goes with the work card */
.work_ex_card h4 {
    position:absolute;
    width: 60%;
    bottom: 50px;
    left: 5px;

    border-top-right-radius: 12.5px;
    border-bottom-right-radius: 12.5px;
    
    padding:2%;
    
    font-size: 20px;
    color: black;

    background-color: rgb(163, 158, 162,.75);
}

